body {
  background-color: rgb(30,32,34);
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Roboto", 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.margintop{
  margin-top: 100px;
}
h6{
  color: white;
}
/** Link */
a {
  color: gray;
}
a:hover{
  color: white;
  text-decoration: none;
}
/** Footer */
.pink{
  height: 15px;
  background: rgb(223,94,133);
}
/** Github + Docker */
.dev {
  width:20px;
  color: white;
}
.nav-link img{
  transition: 0.5s cubic-bezier(0.2, 0.8, 0.2, 1);
}
.nav-link img:hover{
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}
@media (max-width: 400px){
  .dev{
      width: 15px;
  }
}
/** Logo Navbar */
.logo{
  min-width: 100px;
  max-height: 400px;
}
/** Logo Apaxy page */
.logo2{
  width: 180px;
  -webkit-animation: LogoAnimation 3s 0.2s forwards cubic-bezier(0.2, 0.8, 0.2, 1);
          animation: LogoAnimation 3s 0.2s forwards cubic-bezier(0.2, 0.8, 0.2, 1);
  opacity: 0;
}
@-webkit-keyframes LogoAnimation {
  0%{
      opacity: 0;
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
  }
  100%{
      opacity: 1;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
  }
}
@keyframes LogoAnimation {
  0%{
      opacity: 0;
      -webkit-transform: translateY(-15px);
              transform: translateY(-15px);
  }
  100%{
      opacity: 1;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
  }
}
/** Icone animation */
.icons{
  -webkit-animation: IconAnimation 3s 0.2s forwards cubic-bezier(0.2, 0.8, 0.2, 1);
          animation: IconAnimation 3s 0.2s forwards cubic-bezier(0.2, 0.8, 0.2, 1);
  opacity: 0;
}
@-webkit-keyframes IconAnimation {
  0%{
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}
@keyframes IconAnimation {
  0%{
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100%{
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}
/** Screenshot Animation */
.screenshot{
  width: 100%;
  -webkit-animation: ScreenshotAnimation 3s 0.2s forwards cubic-bezier(0.2, 0.8, 0.2, 1);
          animation: ScreenshotAnimation 3s 0.2s forwards cubic-bezier(0.2, 0.8, 0.2, 1);
  opacity: 0;
}
@-webkit-keyframes ScreenshotAnimation {
  0%{
      opacity: 0;
      -webkit-transform: translateY(15px);
              transform: translateY(15px);
  }
  100%{
      opacity: 1;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
  }
}
@keyframes ScreenshotAnimation {
  0%{
      opacity: 0;
      -webkit-transform: translateY(15px);
              transform: translateY(15px);
  }
  100%{
      opacity: 1;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
  }
}
/** Documentation */
.doc{
  color: white;
}
img{
    width: 60px;
    margin: 10px 0;
}

h5{
    color:white;
    font-size: 13px;
}

@media (max-width: 1000px){
    img{
        width: 43px;
    }
    h5{
        font-size: 8px;
    }
}
@media (max-width: 400px){
    img{
        width: 33px;
    }
    h5{
        font-size: 6px;
    }
}

